@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700);
html, body { height: 100%; min-width: 320px;}
body { margin: 0px auto; -webkit-text-size-adjust: none; min-width: 320px; margin: 0 auto; font-family: "Open Sans", sans-serif; line-height: 125%; color: #252525; text-align: center;}

h1, h2, h3, h4, h5, h6 { font-family: "Open Sans", sans-serif; color: #252525; line-height: 125%; }

h1 { font-size: 3rem; margin: 20px 0; font-weight: 500; text-transform:uppercase; letter-spacing: 5px; }
h2 { font-size: 2rem; margin: 10px 0;text-transform:uppercase; letter-spacing: 2px;  }
h3 { font-size: 1.25rem; margin: 10px 0; text-transform:uppercase; letter-spacing: 2px;}
h4 { font-size: 16px; margin: 10px 0; }
h5 { font-size: 14px; margin: 10px 0; font-weight:900;}
h6 { font-size: 12px; margin: 10px 0; }

ul, ol { margin: 0px 0 0px 0px; padding: 0; }
ul li, ol li { margin: 0px 0 0 0px; padding: 0px 0 0 0px; line-height: 140%; }

.btn { cursor: pointer; transition: background-color 0.35s 0.25s, color 0.35s 0.25s; font-size: 0.75rem; padding: 12px 15px; font-family: "Work Sans", sans-serif; display: inline-block; background-color: #555; position: relative; min-width: 120px; color: #fff; text-decoration: none; font-weight: 400; text-align: center; border:1px solid #555;text-transform: uppercase;text-shadow: none;border-radius: 5px; letter-spacing: 3px;}
.btn:hover { transition: background-color 0.35s 0.25s, color 0.35s 0.25s; background-color: transparent;color:#555; }
.btn.alt { color: #555;border:1px solid #555;}
.btn.alt:hover {color: #fff; }

input[type=text] { height:0;outline: none; border: 0px; color: #555; padding: 22px 20px; box-sizing: border-box; line-height: 100%; min-width: 325px; font-weight: 500; font-family: "Open Sans", sans-serif; font-size: 14px;display: inline-block; }

.formGroup{display: inline-block;position: relative;}
.formGroup input[type=text]{border:1px solid #555; border-radius: 5px 0 0 5px; vertical-align: middle;max-height: 46px;}
.formGroup .btn{border-radius: 0 5px 5px 0px; vertical-align: middle;}

#employee-directory{position: absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);display: inline-block;}

.resultDiv ul{margin-bottom:50px;}

.bold{font-weight: bold;}
.fuzzy-search{position:absolute;list-style:none;text-align:left;left:0;right:0;border:1px solid #777;border-top:0;border-radius: 0px 0px 5px 5px;margin-top:-2px;}
.fuzzy-search li{padding:10px 20px;background-color: #fafafa;cursor: default;}
.fuzzy-search li:first-child{padding-top:12px}
.fuzzy-search li:last-child{border-radius: 0px 0px 5px 5px;}
.fuzzy-search li:hover{background-color: #efefef;}


/*Page Loader*/
.page-loader.hide { opacity:0; pointer-events:none; transition: opacity 0.25s;}
.page-loader { opacity: 1; pointer-events: all; position: fixed; left: 0; right: 0; top: 0; bottom: 0; background-color: rgba(42,61,85,1); z-index: 999; transition: opacity 0.25s; }
.page-loader .loader-div { width: 90%; text-align: center; margin-top: -30px; position: absolute; top: 50%; left: 50%; -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%); }
.page-loader .loader-div .loading-msg { max-width: 100%; color: #fff; text-align: center; font-size: 20px; font-weight: 600; text-transform: uppercase; letter-spacing: 0.1875rem; text-shadow: -0.0625rem -0.0625rem 0 #2a3d55, -0.0625rem 0rem 0 #2a3d55, -0.0625rem 0.0625rem 0 #2a3d55, 0rem -0.0625rem 0 #2a3d55, 0rem 0rem 0 #2a3d55, 0rem 0.0625rem 0 #2a3d55, 0.0625rem -0.0625rem 0 #2a3d55, 0.0625rem 0rem 0 #2a3d55, 0.0625rem 0.0625rem 0 #2a3d55; }

/*Page Spinner*/
@-webkit-keyframes page-spinner {
	0% { opacity: 1 }
	100% { opacity: 0 }
}
@keyframes page-spinner {
	0% { opacity: 1 }
	100% { opacity: 0 }
}
.page-spinner div { left: 112.5px; top: 145px; position: absolute; -webkit-animation: page-spinner linear 1.333333333333333s infinite; animation: page-spinner linear 1.333333333333333s infinite; background: #ccbeaf; width: 275px; height: 10px; border-radius: 0px; -webkit-transform-origin: 137.5px 105px; transform-origin: 137.5px 105px; }
.page-spinner div:nth-child(1) { -webkit-transform: rotate(0deg); transform: rotate(0deg); -webkit-animation-delay: -1.1851851851851851s; animation-delay: -1.1851851851851851s; background: #ccbeaf; }
.page-spinner div:nth-child(2) { -webkit-transform: rotate(40deg); transform: rotate(40deg); -webkit-animation-delay: -1.037037037037037s; animation-delay: -1.037037037037037s; background: #ff71a8; }
.page-spinner div:nth-child(3) { -webkit-transform: rotate(80deg); transform: rotate(80deg); -webkit-animation-delay: -0.8888888888888887s; animation-delay: -0.8888888888888887s; background: #7feaff; }
.page-spinner div:nth-child(4) { -webkit-transform: rotate(120deg); transform: rotate(120deg); -webkit-animation-delay: -0.7407407407407406s; animation-delay: -0.7407407407407406s; background: #ccbeaf; }
.page-spinner div:nth-child(5) { -webkit-transform: rotate(160deg); transform: rotate(160deg); -webkit-animation-delay: -0.5925925925925926s; animation-delay: -0.5925925925925926s; background: #ff71a8; }
.page-spinner div:nth-child(6) { -webkit-transform: rotate(200deg); transform: rotate(200deg); -webkit-animation-delay: -0.44444444444444436s; animation-delay: -0.44444444444444436s; background: #7feaff; }
.page-spinner div:nth-child(7) { -webkit-transform: rotate(240deg); transform: rotate(240deg); -webkit-animation-delay: -0.2962962962962963s; animation-delay: -0.2962962962962963s; background: #ccbeaf; }
.page-spinner div:nth-child(8) { -webkit-transform: rotate(280deg); transform: rotate(280deg); -webkit-animation-delay: -0.14814814814814814s; animation-delay: -0.14814814814814814s; background: #ff71a8; }
.page-spinner div:nth-child(9) { -webkit-transform: rotate(320deg); transform: rotate(320deg); -webkit-animation-delay: 0s; animation-delay: 0s; background: #7feaff; }
.page-spinner-wrap { width: 500px; height: 500px; display: inline-block; overflow: hidden; background: rgba(237, 237, 237, 0); }
.page-spinner { width: 100%; height: 100%; position: relative; -webkit-transform: translateZ(0) scale(1); transform: translateZ(0) scale(1); -webkit-backface-visibility: hidden; backface-visibility: hidden; -webkit-transform-origin: 0 0; transform-origin: 0 0;  }
.page-spinner div { box-sizing: content-box; }

